.underline {
  height: 1px;
  width: 80%;
  background: #444;
  background: -webkit-gradient(linear, right top, left top, from(transparent), color-stop(50%, #444), to(transparent));
  background: -webkit-linear-gradient(right, transparent 0%, #444 50%, transparent 100%);
  background: -o-linear-gradient(right, transparent 0%, #444 50%, transparent 100%);
  background: linear-gradient(to left, transparent 0%, #444 50%, transparent 100%);
}



